.ma__about-section__person {
  margin-top: 80px;
}
.ma__header {
  /* transition: ease all 0.4s; */
}
.ma__main-nav__top-link {
  font-size: 1.2rem;
}

.ma__about-section {
  min-height: auto;
  margin-bottom: 60px;
}

.sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  margin-top: 0;
  z-index: 100;
  background: #f2f2f2;
}

.ma__link-list__container {
  display: flex;
  justify-content: center;
  align-items: center;
}
.ma__link-list__items {
  display: inline-block;
}

.button-center {
  margin-top: 5px;
  margin-bottom: 20px;
  text-align: center;
}

.ma__split-columns__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.upcoming-events {
  padding-left: 20px;
  margin-bottom: 40px;
}

.career-video {
  padding-left: 20px;
  margin-right: 80px;
}

.careers-paragraph {
  padding-left: 20px;
  width: 50%;
  margin-bottom: 40px;
}

@media (max-width: 960px) {
  .careers-paragraph {
    width: 100%;
  }
}

.text-center {
  text-align: center;
}

.topbar-hide {
  display: none;
}

.remove-padding {
  padding-top: 0px;
}

.search-services-form {
  max-width: 900px;
}

.search-services {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.service-search-buttons {
  display: flex;
  justify-content: flex-end;
}

.service-search-buttons > * {
  margin-left: 10px;
}

@media (min-width: 1080px) {
  .ma__action-card__item {
    width: 31.7%;
  }
  .ma__action-card__item:nth-child(2n) {
    margin-right: 2.35765%;
  }
  .ma__action-card__item:nth-child(2n + 1) {
    clear: none;
  }
  .ma__action-card__item:nth-child(3n) {
    margin-right: 0px;
  }
}
.show-more-organizations {
  display: none;
}

@media (max-width: 780px) {
  .show-more-organizations {
    display: block;
  }
}

.ma__illustrated-header__content:after {
  background: none;
}

.ma__illustrated-header__content:before {
  position: absolute;
  bottom: 0;
  height: 100%;
  width: 100%;
  content: "";
  background: linear-gradient(
    to top,
    rgb(56 133 87 / 43%) 20%,
    rgb(103 99 99 / 10%) 80%
  );
}

.ma__illustrated-header__image--empty {
  background-color: #388557;
  position: absolute;
  bottom: 0;
  height: 100%;
  width: 100%;
  content: "";
  background: linear-gradient(
    to top,
    rgb(56 133 87 / 43%) 20%,
    rgb(103 99 99 / 10%) 80%
  );
}

.ma__illustrated-header {
  margin: 0;
  margin-bottom: 20px;
}

.ma__input-typeahead--boxed {
  background: white;
}

.ma__header-search {
  max-width: none;
}
.main-content--two .page-content > *,
.main-content--two .page-content .ma__rich-text__container p {
  margin-top: 28px;
  margin-top: 1.75rem;
  margin-bottom: 0px;
}

.ma__comp-heading {
  margin-bottom: 15px !important;
}
